#download {
    cursor: pointer;
    width: 180px;
    height: 40px;
    -webkit-tap-highlight-color: transparent;
    border-radius: 15px;
    flex-direction: row;
    flex-wrap: nowrap;
    transition: background-color .5s ease;
    background-color: #03d19e;
    color: #02503c;
}

#download:hover {
    background-color: #01a87e;
    color: #013729; 
    border: 1px solid #02503c;
}

#btn-download {
    display: block;
    width: 28px;
    height: 20px;
}

#btn-download svg {
    fill: none;
    transform: translate3d(0, 0, 0); 
}

#btn-download svg polyline,
#btn-download svg path {
stroke: #013528;
stroke-width: 2;
stroke-linecap: round;
stroke-linejoin: round;
transition: all .3s ease;
}

#btn-download svg path#check {
    stroke-dasharray: 38;
    stroke-dashoffset: 114;
    transition: all .4s ease;
}
#btn-download.downloaded svg .svg-out {
    opacity: 0;
    animation: drop .3s linear;
    transition-delay: .4s;
}
#btn-download.downloaded svg path#check {
    stroke: #03664f;
    stroke-dashoffset: 174;
    transition-delay: .4s;
}
  
@keyframes drop {
    20% {
        transform: translate(0, -3px); }
    80% {
        transform: translate(0, 2px); }
    95% {
        transform: translate(0, 0); } 
}
  
.credit {
    position: fixed;
    right: 20px;
    bottom: 20px;
    transition: all .2s ease;
    -webkit-user-select: none;
    -moz-user-select: none;
         user-select: none;
    opacity: .6;
}

.credit img {
    width: 72px;
}
.credit:hover {
    transform: scale(0.95);
}

.main-titles {
    color: #163729;
}

.main-desc {
